.contenido_container{
    height: 100%;
    padding: 70px 0;

    .lista_productos{
        display: grid;
        justify-items: center;
        grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
        gap: 30px;
        list-style-type: none;
        width: 70%;
        margin: auto;

        .producto{
            display: flex;
            flex-direction: column;
            text-align: center;
            background-color: rgba(233, 233, 233, .4);
            border-radius: 9px;
            width: 280px;

            .img_producto{
                width: 100%;
                margin: auto;
                height: 250px;
                object-fit: cover;
            }

            .producto_info{
                padding: 8px;
                border: 1px solid black;
                h5{
                    margin: 9px;
                    font-weight: 900;
                    font-size: 14px;
                }

                .boton_producto{
                    color: white;
                    font-weight: 900;
                    background-color: rgb(108, 216, 0);
                    border: none;
                    padding: 6px 20px;
                    border-radius: 16px;
                }
                
                .boton_producto:hover{
                    cursor: pointer;
                    background-color: rgb(93, 185, 0);
                }
            }
        }

        .producto:hover{
            transition: all .8s ease-out;
            transform: scale(110%);
        }
        
    }
}